<template>
  <div class="home">
    <!-- {{this.Fields}} -->
    <div class="cards">
    <SudokuCard v-for="(item, index) in fieldsN(start,end)" :key="item"
    v-bind:stringfield="item"
    v-bind:fieldid="index+start"/>
    </div>
    <button @click="end+=100">+</button>
    <button @click="end-=100">-</button>
  </div>
</template>

<script>
import {
  mapGetters
} from 'vuex'
import SudokuCard from '../components/SudokuCard'
export default {
  name: 'Home',
  data: function () {
    return {
      start: 0,
      end: 100
    };
  },
  components: {
    SudokuCard
  },
  computed: {
    ...mapGetters({
      fieldsN: 'dataManage/fieldsN'
    })
  }
}
</script>
